import Game from './Game';
import { DEFAULT_WIDTH, DEFAULT_HEIGHT, GAME_CONTAINER_ID } from './config';

const containerEl = document.getElementById(GAME_CONTAINER_ID);
const clientWidth = document.body.clientWidth;
const clientHeight = document.body.clientHeight;

let width = DEFAULT_WIDTH;
let height = DEFAULT_HEIGHT;

if (clientWidth < DEFAULT_WIDTH) {
  width = clientWidth;
} 

if (clientHeight < DEFAULT_HEIGHT) {
  height = clientHeight;
}

const game = new Game(width, height);
containerEl.appendChild(game.view);

containerEl.style.width = width + 'px';
containerEl.style.height = height + 'px';



