class Touch {
  constructor(elementId) {
    this.el = document.getElementById(elementId);
    this.isDown = false;
    this.isUp = true;
    this.press = null;
    this.release = null;

    this.el.addEventListener('touchstart', this.handleTouchStart.bind(this));
    this.el.addEventListener('touchend', this.handleTouchEnd.bind(this)); 
  }

  handleTouchStart(e) {
    e.preventDefault();
    this.isUp = false;
    this.isDown = true;
    if (this.press) {
      this.press();
    }
    
  }

  handleTouchEnd(e) {
    e.preventDefault();
    this.isUp = true;
    this.isDown = false;
    if (this.release) {
      this.release();
    }
    
  }
}

export default Touch;


