import Key from './Key';
import Touch from './Touch';
import { keycodes } from './config';

class Input {
  constructor() {
    this.key = {
      left: new Key(keycodes.LEFT),
      right: new Key(keycodes.RIGHT),
      up: new Key(keycodes.UP),
      down: new Key(keycodes.DOWN)
    }

    this.touch = {
        left: new Touch('left'),
        right: new Touch('right'),
        up: new Touch('up'),
        down: new Touch('down')
    }
  }

}

export default Input;


