import Input from './Input';
import PlayState from './PlayState';
import { imageSources } from './config';

class Game {
  constructor(width, height) {
    this.app = new PIXI.Application({
      width: width,
      height: height,
      backgroundColor : 0x9BBC0F,
    });

    this.view = this.app.view;
    this.stage = this.app.stage;
    this.ticker = this.app.ticker;
    this.screen = this.app.screen;
    this.renderer = this.app.renderer;
    this.loader = this.app.loader;

    this.state = null;
    this.update = this.update.bind(this);

    this.input = new Input();
    this.loadImages();
  }

  loadImages() {
    imageSources.forEach( image => {
      this.loader.add(image.name, image.url);
    });

    this.loader.load((loader, resources) => {
      this.state = new PlayState(this);
      this.ticker.add(this.update);
    })
  }

  update(delta) {
    this.state.update(delta);
  }
}

export default Game;


